import { render } from "@testing-library/react";
import { useState } from "react";
import "./Aside.scss";
import AsideButton from "./AsideButton";
import Check from "./Check";
import Line from "./Line";

const Aside = () => {
  const [checks, updateNumOfChecks] = useState(["check"]);

  const addCheck = () => {
    updateNumOfChecks((prevArray) => [...prevArray, "check"]);
  };

  const [lines, updateLines] = useState(["New Line"]);

  const addLine = () => {
    updateLines((prevArray) => [...prevArray, "New Line"]);
  };

  return (
    <aside>
      <div className="content">
        <div className="check-container">

          <h2>Check your journey</h2>

          {checks.map((check, i) => {
            return (
              <>
                <Check key={i} />
              </>
            );
          })}

          <div
            className="check-button-container"
            onClick={addCheck}
            style={{ display: checks.length > 2 ? "none" : "block" }}
          >
            <AsideButton buttonText="Add check" />
          </div>
        </div>

        <div className="line-container">
          <h2>Key to lines</h2>

          {lines.map((line, i) => {
            const colors = ["red", "blue", "yellow", "green", "grey"];
            let randomColor = colors[Math.floor(Math.random() * colors.length)];
            console.log(randomColor);
            return <Line key={i} backgroundColor={randomColor} />;
          })}

          <div
            onClick={() => {
              addLine();
            }}
          >
            <AsideButton
              className="check-button-container"
              buttonText="Add Line"
              marginTop="30px"
            />
          </div>
        </div>
      </div>
      <footer>
        <div>
          <p>Finished?</p>
          <p>Export</p>
        </div>
        <i class="fas fa-arrow-right"></i>
      </footer>
    </aside>
  );
};

export default Aside;
